import { NgModule } from '@angular/core';
import { PreloadAllModules, RouterModule, Routes } from '@angular/router';

const routes: Routes = [
  {
    path: 'home',
    loadChildren: () => import('./home/home.module').then( m => m.HomePageModule)
  },
  {
    path: '',
    redirectTo: 'home',
    pathMatch: 'full'
  },
  {
    path: 'lounge',
    loadChildren: () => import('./pages/lounge/lounge.module').then( m => m.LoungePageModule)
  },
  {
    path: 'battlefield',
    loadChildren: () => import('./pages/battlefield/battlefield.module').then( m => m.BattlefieldPageModule)
  },
  {
    path: 'card-viewer',
    loadChildren: () => import('./pages/card-viewer/card-viewer.module').then( m => m.CardViewerPageModule)
  },
];

@NgModule({
  imports: [
    RouterModule.forRoot(routes, { preloadingStrategy: PreloadAllModules })
  ],
  exports: [RouterModule]
})
export class AppRoutingModule { }
