import { HttpClient, HttpHeaders } from '@angular/common/http'
import { Platform } from '@ionic/angular'
import { Injectable } from '@angular/core'

import { Animation, AnimationController } from '@ionic/angular'

import {
  User,
  Http,
  Dice,
  Board,
  Map,
  Player,
  DEFAULT_RED,
  DEFAULT_BLUE,
  Storage,


  Room,
  Room0,
  Room1,

  TUTORIAL,
  Tutorial,

  Audio
} from 'dice-guardians-module'


@Injectable({
  providedIn: 'root'
})
export class DiceGuardiansModuleService {
  httpClient:HttpClient

  http:Http

  user:User
  board:Board
  map:Map

  baseUrl:string
  platform:Platform
/*
  storage:Storage = new Storage(
    localStorage,
    ['cards', 'abilities']
  )
  */

  animationCtrl: AnimationController

  constructor(
    httpClient:HttpClient,
    animationCtrl: AnimationController,
    platform:Platform
  ) {
    const self = this
    this.platform = platform

    this.animationCtrl = animationCtrl


    this.map = new Map(this, [
      //Room0
      Room1
    ])

    this.board = new Board(
      this,
      [Room0, Room1],
      {
        red : new Player(DEFAULT_RED, this),
        blue : new Player(DEFAULT_BLUE, this)
      },
      true
    )


    this.board.players.red.energyPool.mint('water', 3)
    this.board.players.red.energyPool.mint('wood', 3)
    this.board.players.red.energyPool.mint('neutral', 1)

    this.board.players.blue.energyPool.mint('fire', 3)
    this.board.players.blue.energyPool.mint('metal', 1)
    this.board.players.blue.energyPool.mint('light', 2)


    this.platform.ready().then(() => {
      this.board.events.emit('ready', {})
    })
    /*
    this.httpClient = httpClient

    this.baseUrl = 'http://' + window.location.hostname + ':3000/'

    this.http = Http(this)
    // instantiate the goddamn user
    this.user = new User(this)*/

  }
}
